import { socket } from '../../Room';

const MoveBox = (entities, {input}) => {
    const { payload } = input.find(x => x.name === 'onMouseDown') || {};

    if (payload) {

        //the users controlled char
        const myPlayer = entities['myPlayer'];

        //clear intervals from previous movement commands
        let highestIntervalId = setInterval(";");
        for (var i = 0 ; i < highestIntervalId ; i++) {
            clearInterval(i); 
        }

        //calculate velocity
        let dx = payload.pageX - myPlayer.x;
        let dy = payload.pageY - myPlayer.y;
        const angle = Math.atan2(dy, dx);

        myPlayer.xVelocity = Math.cos(angle) * 6;
        myPlayer.yVelocity = Math.sin(angle) * 6;

        let moveTimer = setInterval(() => {
            if (((dx > 0 && myPlayer.x >= payload.pageX) || (dx < 0 && myPlayer.x <= payload.pageX)) && 
                ((dy > 0 && myPlayer.y >= payload.pageY) || (dy < 0 && myPlayer.y <= payload.pageY))) clearInterval(moveTimer);

            
            if ((dx < 0 && myPlayer.x - payload.pageX > 0) || (dx > 0 && myPlayer.x - payload.pageX < 0)) myPlayer.x = myPlayer.x + myPlayer.xVelocity;
            if ((dy < 0 && myPlayer.y - payload.pageY > 0) || (dy > 0 && myPlayer.y - payload.pageY < 0)) myPlayer.y = myPlayer.y + myPlayer.yVelocity;
            const data = {
                position: {
                    x: myPlayer.x,
                    y: myPlayer.y
                }
            }
            socket.emit('updatePosition', data);
        }, 50)
        entities['myPlayer'] = myPlayer;
    }

    /*if (movedPlayer && entities[movedPlayer['id']]) {
        entities[movedPlayer['id']]['x'] = movedPlayer['position']['x'];
        entities[movedPlayer['id']]['y'] = movedPlayer['position']['y'];
    }*/

    return entities;
}

export { MoveBox };