import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { GameEngine } from 'react-game-engine';
import { MoveBox } from '../systems/MoveBox';
import Chat from '../Chat';

import openSocket from 'socket.io-client';

import Character from '../character';

const socket = openSocket('https://katyosh-server.herokuapp.com/');

class SimpleGame extends Component {

    constructor(props) {
        super(props);
        this.state = {
            entities: {},
            loading: true
        }
    }

    componentDidMount() {
        const { avatar, myName } = this.props.location;
        if(!avatar || !myName) this.props.history.push('/party-hard')
        const initData = {
            avatar: avatar,
            name: myName
        }
        socket.emit('startGame', initData);

        socket.on("connect", () => {
            console.log("Connected to: heroku");
        });

        socket.on('register', (player) => {
            const myPlayer = {x: 200, y: 200, xVelocity: 0, yVelocity: 0, avatar: avatar, name: myName, renderer: <Character avatar={avatar} name={myName} />};
            let entities = this.state.entities;
            entities = {...entities, myPlayer};
            
            this.setState({entities, loading: false});
            
        });
    
        socket.on('spawn', (player) => {
            const { id, avatar, name } = player;
            const newPlayer = {x: 200, y: 200, xVelocity: 0, avatar: avatar, name: name, yVelocity: 0, renderer: <Character avatar={avatar} name={name} />};
            let entities = this.state.entities;
            entities[id] = newPlayer;
            this.setState({entities});
        });

        socket.on('updatePosition', (data) => {
            const {id, position} = data;
            let entities = this.state.entities;
            entities[id]['x'] = position.x;
            entities[id]['y'] = position.y;
            this.setState({entities})
        })

        socket.on('disconnected', player => {
            let entities = this.state.entities;
            delete entities[player.id];
            this.setState({entities})
        })
    }

    render() {
        if (this.state.loading) return (<p>loading</p>);
        return(
            <div style={{backgroundColor: '#222'}}>
                <GameEngine
                    style={{width: '100%', height: 600, backgroundImage: 'url(https://static.turbosquid.com/Preview/2014/08/01__18_23_50/rockGray2048.png8a9f8e8d-ce46-4604-bc94-71fca8eab578Original.jpg)', backgroundRepeat: 'repeat'}}
                    systems={[MoveBox]}
                    entities={this.state.entities}
                >
        
                </GameEngine>
                <Chat />
            </div>
        );
    }
}

export { socket };

export default withRouter(SimpleGame);
