import React from 'react';
import styled from 'styled-components';

import { characterImages } from './data';

const CharName = styled.p `
    font-weight: bold;
    -webkit-text-stroke: 1px black;
    color: orange;
    font-size: 24px;
    margin: 0;
`

const Box = (props) => {
    
    const size = 35;
    const x = props.x - size / 2;
    const y = props.y - size / 2;
    if (props.name && props.avatar) return(
        <div style={{ display: "flex", flexDirection: 'column', alignItems: "center", justifyContent: "center", position: "absolute", width: size, height: size + 10, background: "none", left: x, top: y }}>
            <CharName>{props.name}</CharName>
            <img alt="you" src={characterImages[props.avatar]} />
        </div>
    );
    return (null);
}

export default Box;