import React, { useContext, Component } from 'react';
import { withRouter } from 'react-router-dom';
import styled from 'styled-components';

import { characterImages } from './data';

const Selector = styled.li `
    background-color: cornsilk;
    padding: 5px;
    border-radius: 5px;
    border: 1px solid #aaa;
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;

    ${({ active }) => active && `
    background-color: crimson;
  `}
`

const SelectorList = styled.ul `
    list-style: none;
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-around;
`

const NameSection = styled.section `
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
`

class WelcomeScreen extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            avatar: '01',
        }
    }

    navTo = () => {
        this.props.history.push({
            pathname: '/party-hard/room',
            avatar: this.state.avatar,
            myName: this.state.name
        })
    }

    setAvatar = (id) => {
        this.setState({avatar: id})
    }


    render() {
        return(
            <div style={{backgroundColor: '#222'}}>
                <h1 style={{textAlign: "center", color: 'antiquewhite'}}>Choose your avatar</h1>
                <section style={{display: 'flex'}}>
                    <SelectorList style={{listStyle: 'none'}}>
                        <Selector active={this.state.avatar === '01'} onClick={() => {this.setAvatar('01')}}>
                            <img alt="avt" src={characterImages["01"]} />
                        </Selector>
                        <Selector active={this.state.avatar === '02'} onClick={() => {this.setAvatar('02')}}>
                            <img alt="avt" src={characterImages["02"]} />
                        </Selector>
                        <Selector active={this.state.avatar === '03'} onClick={() => {this.setAvatar('03')}}>
                            <img alt="avt" src={characterImages["03"]} />
                        </Selector>
                        <Selector active={this.state.avatar === '04'} onClick={() => {this.setAvatar('04')}}>
                            <img alt="avt" src={characterImages["04"]} />
                        </Selector>
                        <Selector active={this.state.avatar === '05'} onClick={() => {this.setAvatar('05')}}>
                            <img alt="avt" src={characterImages["05"]} />
                        </Selector>
                        <Selector active={this.state.avatar === '06'} onClick={() => {this.setAvatar('06')}}>
                            <img alt="avt" src={characterImages["06"]} />
                        </Selector>
                    </SelectorList>
                </section>
                <NameSection>
                    <h1 style={{textAlign: "center", color: 'antiquewhite'}}>Enter your name</h1>
                    <input value={this.state.name} onChange={(e) => this.setState({name: e.target.value})} />
                    <button disabled={this.state.name === ''} onClick={() => this.navTo()}>Start</button>
                </NameSection>
            </div>
        )
}}

export default withRouter(WelcomeScreen);