import React, { Component } from 'react';
import { socket } from '../Room';
import Message from './Message';
import styled from 'styled-components';

const SubmitMessage = styled.button `
    width: 8%;
    height: 25px;
    color: orange;
    background-color: black;
    font-weight: bold;
    font-size: 14px;
    border-color: orange;
`

const MessageInput = styled.input `
    width: 80%;
    height: 25px;
`

const ChatLabel = styled.label `
    width: 10%;
    color: orange;
    font-weight: bold;
    padding-left: 5px;
    padding-right: 5px;
`

class Chat extends Component {
    constructor(props) {
        super(props);

        this.state = {
            messages: [],
            currentMessage: ''
        }

        this.logRef = React.createRef();
    }

    componentDidMount() {
        socket.on('textMessage', (data) => {
            const messages = this.state.messages;
            messages.push({
                text: data.text,
                sender: data.name
            })
            this.setState({messages})
            const log = document.getElementById("chatlog");
            log.scrollTop = log.scrollHeight;
        })
    }

    onSubmit = (e) => {
        e.preventDefault();
        socket.emit('textMessage', this.state.currentMessage);
        this.setState({currentMessage: ''})
    }

    renderMessages = () => {
        return this.state.messages.map((message, i) => <Message key={i} sender={message.sender} text={message.text} />)
    }

    render() {
        return(
            <div style={{border: '3px solid #cab', borderRadius: '5px'}}>
                <div id='chatlog' scrollTop={'100'} style={{backgroundColor: 'cornsilk', maxHeight: '400px', height: '300px', resize: 'vertical', overflow: 'auto'}}>
                    {this.renderMessages()}
                </div>
                <div>
                    <form onSubmit={this.onSubmit}>
                        <ChatLabel>CHAT:</ChatLabel>
                        <MessageInput value={this.state.currentMessage} type='text' onChange={(e) => this.setState({currentMessage: e.target.value})} />
                        <SubmitMessage type='submit'>Send</SubmitMessage>
                    </form>
                </div>
            </div>
        )
    }
}

export default Chat;