import React from 'react';

import { BrowserRouter, Switch, Route } from 'react-router-dom';

import Room from './Room';
import WelcomeScreen from './WelcomeScreen';

const Router = () => {
    return(
        <BrowserRouter>
        <div className='Page'>
            <Switch>
                <Route exact path='/party-hard' render={() => <WelcomeScreen />} />
                <Route path='/party-hard/room' render={() => <Room />} />
            </Switch>
        </div>
    </BrowserRouter>
    )
}

export default Router;